.ServicesSection{
  text-align: center;
  color: #555;
}
.ServicesSection h2{
  margin: 1.5em 0 0;
}
.ServicesSection h2,
.ServicesSection h3{
  font-weight: 100;
}
.ServicesWrp{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 0 5%;
  -webkit-transition: opacity 4s;
  -o-transition: opacity 4s;
  transition: opacity 4s;
  opacity: 0;
}
@media (min-width:1367px){
  .ServicesWrp{
    margin-top: 10vh;
  }
}
.ServicesWrpActive{
  opacity: 1;
}
.ServiceContainer{
  position: relative;
  color: #eee;
  margin: 2%;
  padding-top: 10%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  cursor: pointer;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  -o-transition: transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  -webkit-transform: translate(800%, 0);
      -ms-transform: translate(800%, 0);
          transform: translate(800%, 0);
  line-height: 4vh;
}
.ServiceContainerActive{
  -webkit-transform: translate(0);
      -ms-transform: translate(0);
          transform: translate(0);
}
.FrontSide::before,
.BackSide::before{
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-image: -webkit-gradient(linear ,left top, left bottom ,from(#000),to(#000));
  background-image: -webkit-linear-gradient(top ,#000,#000);
  background-image: -o-linear-gradient(top ,#000,#000);
  background-image: linear-gradient(to bottom ,#000,#000);
	opacity: .68;
}
.ServiceContainer:hover .FrontSide{
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}
.ServiceContainer:hover .BackSide{
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}
.ServiceTitle{
  font-size: 1.3em;
  font-weight: 600;
}
.ServiceTitle,
.ServiceInfo{
  position: relative;
  z-index: 1;
}
.InfoWrp{
  position: relative;
  z-index: 2;
  -webkit-perspective: inherit;
          perspective: inherit;
  -webkit-transform: translateZ(60px) scale(0.85);
          transform: translateZ(60px) scale(0.85);
}
.FrontSide .InfoWrp{
  margin-top: 40%;
}
.BackSide .InfoWrp{
  margin-top: 35%;
}
.Divider{
  display: inline-block;
  width: 0;
  height: 3px;
  background-image: -webkit-gradient(linear, left top, right top, from(#39dfa5), to(#1fbdf2));
  background-image: -webkit-linear-gradient(left, #39dfa5, #1fbdf2);
  background-image: -o-linear-gradient(left, #39dfa5, #1fbdf2);
  background-image: linear-gradient(to right, #39dfa5, #1fbdf2);
  -webkit-transition: width 2.4s ease-out;
  -o-transition: width 2.4s ease-out;
  transition: width 2.4s ease-out;
  -webkit-transition-delay: .5s;
       -o-transition-delay: .5s;
          transition-delay: .5s;
}
.DividerActive{
  width: 35%;
}
.FrontSide{
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}
.BackSide{
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.FrontSide,
.BackSide{
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  -webkit-transition: -webkit-transform .85s;
  transition: -webkit-transform .85s;
  -o-transition: transform .85s;
  transition: transform .85s;
  transition: transform .85s, -webkit-transform .85s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.Title{
  -webkit-transform: translate(0, -800%);
      -ms-transform: translate(0, -800%);
          transform: translate(0, -800%);
  -webkit-transition: -webkit-transform 1.4s ease-out;
  transition: -webkit-transform 1.4s ease-out;
  -o-transition: transform 1.4s ease-out;
  transition: transform 1.4s ease-out;
  transition: transform 1.4s ease-out, -webkit-transform 1.4s ease-out;
}
.TitleActive{
  -webkit-transform: translate(0);
      -ms-transform: translate(0);
          transform: translate(0);
}
.Sentence{
  -webkit-transform: translate(-120%, 0);
      -ms-transform: translate(-120%, 0);
          transform: translate(-120%, 0);
  opacity: 0;
  -webkit-transition: all 1.2s ease-out;
  -o-transition: all 1.2s ease-out;
  transition: all 1.2s ease-out;
}
.SentenceActive{
  -webkit-transform: translate(0);
      -ms-transform: translate(0);
          transform: translate(0);
  opacity: 1;
}
@media (max-width:768px){
  .ServicesWrp{
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 0 10%;
  }
  .ServiceContainer{
    margin: 4% 0;
  }
  .BackSide .InfoWrp{
    margin-top: 20%;
  }
  .FrontSide .InfoWrp{
    margin-top: 25%;
  }
}
@media (max-height:450px){
  .ServiceContainer{
    line-height: 1.3;
    padding: 0;
  }
  .BackSide .InfoWrp{
    margin-top: 20%;
  }
  .FrontSide .InfoWrp{
    margin-top: 25%;
  }
}



